.Experience {
	padding: 0px 40px;
	margin-top: 20px;
	h1 {
		text-align: center;
		font-size: 25px;
		text-transform: uppercase;
		font-family: sans-serif;
		font-weight: bold;
	}

	.table {
		margin-top: 40px;
		width: 100%;
		.td {
			.tr {
				text-align: left;
				height: 60px;
				color: rgb(112, 112, 112);
				font-size: 18px;
			}
			.t1 {
				color: black;
				font-weight: bold;
			}
		}
		.showonchoto{
			display: none;
		}
	}
}
@media screen and (max-width : 700px) {
	.Experience{
		margin-top: 100px;
		padding : 0px 20px;
		h1{
			font-size: 16px!important;

		}
		.table{
			.t2,.t3{
				display: none;
			}
			.showonchoto{
				display: block;
				font-size: 15px;
				color: rgb(177, 177, 177);
				margin-top: 5px;
				margin-bottom: 40px;
			}
		}
	}
}