.Slider {
	padding: 40px;
	height: 550px;
	position: relative;
	.swiper-container {
		display: flex;
		flex-direction: column-reverse;
	}
	



	.swiper-container {
		width: 100%;
		height: 100%;
	}

	.swiper-slide {
		text-align: center;
		font-size: 18px;
		background: #fff;

		/* Center slide text vertically */
		display: -webkit-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		-webkit-justify-content: center;
		justify-content: center;
		-webkit-box-align: center;
		-ms-flex-align: center;
		-webkit-align-items: center;
		align-items: center;
	}

	.swiper-slide img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.swiper-container {
		margin-left: auto;
		margin-right: auto;
	}
	.swiper-pagination {
		height: 40px;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
	}
	.swiper-pagination-bullet {
		margin: 0px 5px;
		color: rgb(161, 161, 161);
		font-weight: bold;
		display: flex;
		justify-content: right;
		align-items: center;
		transition: 0.5s;
		text-align: center;
		.activebar {
			margin-left: 5px;
		}
		cursor: pointer;
	}
	.swiper-pagination-bullet.swiper-pagination-bullet-active {
		color: black;
		transition: 0.5s;

		div {
			height: 2px;
			border-radius: 30px;
			background-color: rgb(68, 68, 68);
			animation: bulletAnimation 3s linear;
		}
	}
	.sliderX {
		background-color: rgb(206, 206, 206);
	}
}
@keyframes bulletAnimation {
	0% {
		width: 0px;
	}
	8% {
		width: 75px;
	}
	90% {
		width: 75px;
	}
	100% {
		width: 0px;
	}
}
@media screen and (max-width : 700px) {
	.Slider{
		padding : 0px 20px;
		height: 250px;
		margin-bottom: 100px;
		margin-top: 50px;
	}
}