.about {
	.Header {
		text-align: center;
		padding: 0px 150px;
		padding-top: 60px;
		margin-bottom: 150px;
		.Afewword {
			font-size: 14px;
			letter-spacing: 8px;
			font-weight: 300;
			font-family: sans-serif;
			color: rgb(124, 124, 124);
		}
		.boldtext {
			font-family: sans-serif;
			font-size: 45px;
			line-height: 50px;
		}
		.lowtext {
			font-size: 22px;
			margin-top: 40px;
			color: gray;
			line-height: 35px;
			a {
				text-decoration: none;
				color: rgb(83, 83, 83);
				border-bottom: 1px solid gray;
				transition: 0.2s;
				font-weight: 600;
				&:hover {
					color: black;
				}
			}
		}
	}

	.aboutService {
		padding: 0px 40px;
		margin-top: 50px;
		display: flex;
		justify-content: center;
        
		.services {
			p {
				font-size: 16px;
				color: gray;
				text-transform: uppercase;
				margin-bottom: 20px;
				letter-spacing: 6px;
				font-weight: 600;
			}
			width: 50%;
			text-align: center;
			padding: 100px;
			color: rgb(27, 27, 27);
			font-weight: bolder;
			text-transform: uppercase;
			h1 {
				font-family: 'Roboto', sans-serif;
				font-size: 20px;
				padding: 30px;
			}
			em {
				margin: 0px 10px;
				color: rgb(151, 151, 151);
			}
		}
		.aboutserviceimg {
			width: 50%;
			img {
				width: 100%;
			}
		}
	}
}

@media screen and (max-width: 700px) {
	.about {
		.Header {
			padding: 0px 20px;
			margin-bottom: 100px;
			margin-top: 100px;

			.boldtext {
				font-size: 20px;
				line-height: 28px;
				margin-bottom: 60px;
				margin-top: 40px;
			}
			.Afewword {
				font-size: 14px;
				letter-spacing: 4px;
			}
			.lowtext {
				font-size: 16px;
				line-height: 24px;
			}
		}
		.aboutService {
			padding: 0px;
            width: 100%;
            overflow: hidden;
            flex-direction: column;

			.services {
                width: 100%;
                padding: 0px;
                p{
                    margin: 0px;
                    padding: 0px;
                    margin-bottom: 20px;
                    margin-top: 20px;
                }
                h1{
                    margin: 0;
                    padding: 30px;
                    padding-top: 0px;

                }
			}
            .aboutserviceimg{
                width: 80%;

                margin : auto;
                margin-bottom: 20px;
            }
            
		}
        .aboutService.firstone{
            flex-direction: column-reverse;
        }
	}
}
