@media screen and (min-width : 1200px) {
	.Container {
		max-width: 100%;
        padding: 0px 40px;
	}
}
@media screen and (min-width : 1400px) {
	.Container {
		max-width: 1200px;
        padding: 0px 40px;
	}
}

.body-main{
	max-width: 1400px!important;
	margin: auto;
}
.body-box{
	padding : 0px 40px;
}

@media screen and (max-width) {
	.body-box{
		padding : 0px 20px;
	}
}